"use client";

import { useState, useEffect, Suspense } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { toast } from "react-hot-toast";
import { api } from "@/lib/api";
import { 
  Hash, DollarSign, ArrowLeft, Info, ShoppingBag, 
  ShieldCheck, Loader2, ChevronRight, Zap, 
  Calculator, MessageSquare
} from "lucide-react"; // Note: Using lucide-react as standard
import Link from "next/link";

function RFQForm() {
  const router = useRouter();
  const params = useSearchParams();
  const productId = params.get("productId");

  const [product, setProduct] = useState<any>(null);
  const [quantity, setQuantity] = useState("");
  const [targetPrice, setTargetPrice] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const [fetchingProduct, setFetchingProduct] = useState(true);

  useEffect(() => {
    if (!productId) {
      toast.error("No product selected.");
      router.push("/dashboard");
      return;
    }

    api.get(`/products/${productId}`)
      .then(res => {
        setProduct(res.data);
        setQuantity(res.data.minimumOrderQuantity?.toString() || "");
        setTargetPrice(res.data.pricePerUnit?.toString() || "");
      })
      .catch((err) => {
        toast.error("Could not load product details");
        router.push("/dashboard");
      })
      .finally(() => setFetchingProduct(false));
  }, [productId, router]);

  const submitRFQ = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!productId) return toast.error("Product information missing.");
    
    if (product && Number(quantity) < product.minimumOrderQuantity) {
      return toast.error(`Minimum order is ${product.minimumOrderQuantity} units`);
    }

    setLoading(true);
    try {
      await api.post("/rfq", {
        productId,
        quantity: Number(quantity),
        targetUnitPrice: targetPrice ? Number(targetPrice) : undefined,
        message,
      });
      
      toast.success("Negotiation Initialized!");
      router.push("/buyer/rfq/list");
    } catch (err: any) {
      toast.error(err.response?.data?.message || "Failed to submit RFQ");
    } finally {
      setLoading(false);
    }
  };

  if (fetchingProduct) return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-[#020617]">
      <div className="w-12 h-12 border-4 border-blue-500/10 border-t-blue-500 rounded-full animate-spin mb-4" />
      <p className="text-[10px] font-black uppercase tracking-[0.3em] text-blue-400/50">Encrypting Marketplace Data</p>
    </div>
  );

  const currentTotal = (Number(quantity) || 0) * (Number(targetPrice) || 0);

  return (
    <div className="min-h-screen bg-[#020617] text-slate-100 pb-20 selection:bg-blue-500/30">
      {/* HEADER SECTION */}
      <div className="bg-[#0F172A]/80 border-b border-blue-900/30 px-8 py-6 sticky top-0 z-50 backdrop-blur-xl">
        <div className="max-w-7xl mx-auto flex items-center justify-between">
          <Link href="/dashboard" className="flex items-center gap-2 group">
            <div className="p-2 bg-blue-900/20 rounded-lg group-hover:bg-blue-800/40 transition-all border border-blue-800/20">
              <ArrowLeft size={16} className="text-blue-400" />
            </div>
            <span className="text-[10px] font-black uppercase tracking-widest text-slate-400 group-hover:text-blue-400 transition-colors">Discard Draft</span>
          </Link>
          <div className="flex items-center gap-6">
             <div className="flex items-center gap-2 text-emerald-400 bg-emerald-500/10 px-4 py-2 rounded-full border border-emerald-500/20">
                <ShieldCheck size={14} className="animate-pulse" />
                <span className="text-[9px] font-black uppercase tracking-widest">Escrow Active</span>
             </div>
          </div>
        </div>
      </div>

      <main className="max-w-7xl mx-auto px-8 mt-12">
        <div className="grid grid-cols-1 lg:grid-cols-12 gap-12">
          
          {/* LEFT: PRODUCT INTELLIGENCE */}
          <div className="lg:col-span-4 space-y-6 lg:sticky lg:top-32 self-start">
            <div className="bg-[#0F172A] rounded-[2.5rem] border border-blue-900/30 overflow-hidden shadow-2xl">
               <div className="aspect-square bg-slate-900 relative group">
                  {product?.images?.[0] ? (
                    <img src={product.images[0]} className="w-full h-full object-cover opacity-80 group-hover:opacity-100 transition-opacity" alt="" />
                  ) : (
                    <div className="w-full h-full flex items-center justify-center text-blue-900"><ShoppingBag size={48} /></div>
                  )}
                  <div className="absolute bottom-4 left-4 right-4 bg-[#1E293B]/90 backdrop-blur-md p-5 rounded-2xl border border-blue-500/20 shadow-xl">
                    <p className="text-[9px] font-black text-blue-400 uppercase tracking-widest mb-1">Standard Market Rate</p>
                    <p className="text-2xl font-[1000] tracking-tighter text-white">${product?.pricePerUnit}<span className="text-xs text-slate-500 font-bold ml-1">/unit</span></p>
                  </div>
               </div>
               <div className="p-8">
                  <h1 className="text-xl font-black tracking-tight text-white mb-2">{product?.name}</h1>
                  <p className="text-sm text-slate-400 font-medium leading-relaxed mb-8 line-clamp-3">{product?.description}</p>
                  <div className="flex items-center justify-between p-5 bg-[#1E293B]/50 rounded-2xl border border-blue-900/20">
                     <span className="text-[10px] font-black text-blue-400 uppercase tracking-widest">MOQ Requirement</span>
                     <span className="text-sm font-black text-white">{product?.minimumOrderQuantity} Units</span>
                  </div>
               </div>
            </div>

            {/* LIVE CALCULATION CARD */}
            <div className="bg-blue-600 rounded-[2.5rem] p-8 text-white shadow-2xl shadow-blue-600/20 relative overflow-hidden group">
               <Zap className="absolute -right-4 -top-4 w-32 h-32 text-white/10 rotate-12 group-hover:rotate-0 transition-transform duration-700" />
               <p className="text-[10px] font-black uppercase tracking-[0.2em] text-blue-100/60 mb-2">Projected Value</p>
               <h3 className="text-5xl font-[1000] tracking-tighter">${currentTotal.toLocaleString()}</h3>
               <div className="mt-8 flex items-center gap-3 p-3 bg-black/20 rounded-xl border border-white/10 backdrop-blur-md">
                  <Calculator size={16} className="text-blue-200" />
                  <p className="text-[10px] font-bold text-blue-50 tracking-tight uppercase italic">Live terminal calculation</p>
               </div>
            </div>
          </div>

          {/* RIGHT: NEGOTIATION FORM */}
          <div className="lg:col-span-8">
            <div className="bg-[#0F172A] rounded-[3rem] border border-blue-900/30 p-10 md:p-16 shadow-2xl relative">
              <div className="mb-12">
                <div className="w-12 h-1 bg-blue-500 rounded-full mb-6" />
                <h2 className="text-4xl font-[1000] tracking-tighter text-white mb-3">Initialize Negotiation</h2>
                <p className="text-slate-400 text-sm font-medium tracking-wide">Enter your proposed volume and pricing structure below.</p>
              </div>

              <form onSubmit={submitRFQ} className="space-y-10">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                  <div className="space-y-4">
                    <label className="text-[11px] font-black uppercase tracking-widest text-blue-400/80 ml-1">Total Quantity</label>
                    <div className="group relative">
                      <Hash className="absolute left-6 top-1/2 -translate-y-1/2 text-slate-500 group-focus-within:text-blue-400 transition-colors" size={20} />
                      <input
                        type="number"
                        required
                        value={quantity}
                        onChange={(e) => setQuantity(e.target.value)}
                        className="w-full pl-14 pr-6 py-6 bg-[#1E293B]/40 border-2 border-transparent rounded-[1.5rem] focus:bg-[#1E293B] focus:border-blue-500 transition-all outline-none font-black text-2xl text-white placeholder:text-slate-700"
                        placeholder="0"
                      />
                    </div>
                  </div>

                  <div className="space-y-4">
                    <label className="text-[11px] font-black uppercase tracking-widest text-blue-400/80 ml-1">Offered Unit Price</label>
                    <div className="group relative">
                      <DollarSign className="absolute left-6 top-1/2 -translate-y-1/2 text-slate-500 group-focus-within:text-blue-400 transition-colors" size={20} />
                      <input
                        type="number"
                        step="0.01"
                        required
                        value={targetPrice}
                        onChange={(e) => setTargetPrice(e.target.value)}
                        className="w-full pl-14 pr-6 py-6 bg-[#1E293B]/40 border-2 border-transparent rounded-[1.5rem] focus:bg-[#1E293B] focus:border-blue-500 transition-all outline-none font-black text-2xl text-white placeholder:text-slate-700"
                        placeholder="0.00"
                      />
                    </div>
                  </div>
                </div>

                <div className="space-y-4">
                  <label className="text-[11px] font-black uppercase tracking-widest text-blue-400/80 ml-1 flex items-center gap-2">
                    <MessageSquare size={14} /> Negotiation Memo (Optional)
                  </label>
                  <textarea
                    rows={4}
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                    className="w-full px-8 py-6 bg-[#1E293B]/40 border-2 border-transparent rounded-[1.5rem] focus:bg-[#1E293B] focus:border-blue-500 transition-all outline-none resize-none font-medium text-slate-300 placeholder:text-slate-700"
                    placeholder="Briefly describe shipping terms or volume commitments..."
                  />
                </div>

                <div className="pt-6">
                  <button
                    disabled={loading}
                    className="w-full py-6 bg-white text-[#020617] hover:bg-blue-400 transition-all font-[1000] rounded-[2rem] shadow-2xl shadow-white/5 active:scale-[0.98] disabled:opacity-50 flex justify-center items-center gap-3 text-lg group uppercase tracking-widest"
                  >
                    {loading ? (
                      <Loader2 className="animate-spin" />
                    ) : (
                      <>
                        Push Proposal
                        <ChevronRight size={20} className="group-hover:translate-x-1 transition-transform" />
                      </>
                    )}
                  </button>
                  <p className="text-center mt-8 text-[9px] font-black text-slate-600 uppercase tracking-[0.3em]">
                    Enterprise-Grade Encryption Secured By ProcureOS
                  </p>
                </div>
              </form>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}

export default function CreateRFQPage() {
  return (
    <Suspense fallback={<div className="min-h-screen bg-[#020617] flex items-center justify-center font-black text-blue-900 tracking-tighter text-4xl">SYNCING...</div>}>
      <RFQForm />
    </Suspense>
  );
}